import { NgModule, Component, ElementRef, ViewChild } from '@angular/core';
import { ReactiveFormsModule , FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NGXFormWizardModule } from "./ngx-wizard/ngx-wizard.module";
import { FormsRoutes } from './forms.routing';
import { BasicComponent } from './form-basic/basic.component';
import { FormvalComponent } from './form-validation/form-validation.component';
import { MultiselectComponent } from './multiselect/multiselect.component';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { CustomFormsModule } from 'ng2-validation';
import { ArchwizardModule } from 'ng2-archwizard';
@NgModule({
  imports: [CommonModule, RouterModule.forChild(FormsRoutes), FormsModule,CustomFormsModule, NgbModule, ReactiveFormsModule, NgMultiSelectDropDownModule.forRoot(), ArchwizardModule, NGXFormWizardModule],
  declarations: [
    BasicComponent,
    FormvalComponent,
    MultiselectComponent
    
  ]
})
export class FormModule { }
